html, body, div, header, nav, ul, li, a, img, p, footer, h1, h2, h3 {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
  color: #000;
}
footer {
  background-color: #bda68a;
  height: 130px;
  border-top: 1px solid;
  margin-top: 40px;
} 
.footer1 {
  display: flex;
  flex-direction: row;
  width: 960px;
}
.footer1 p {
  text-align: left;
  margin: 15px 300px 0 50px;
  font-size: 0.9em;
  font-weight: bold;
}
.footer1 img {
  width: 120px;
  margin: 0 0 0 150px;
}
.footer2 {
  text-align: center;
  width: 960px;
  margin: auto;
  border-top: 2px solid;
  font-size: 0.6em;
}
header {
  width: 100%;
}
#menu {
  display: flex;
  flex-direction: row;
  text-align: center;
  margin: 40px auto;
  width: 960px;
  height: 80px;
  background-color: #bda68a;
  border-radius: 15px;
}

#menu li {
  margin: auto;
  width: 200px;
  padding: 15px 0 15px 0;
}
#menu li a:hover {
  color: white;
}

body {
  background-color: rgb(253, 230, 192);
}

#body {
  width: 960px;
  margin: auto;
  height: 100%;
}
#main_body{
  width: 960px;
}
.mainBody {
  height: 960px;
}
.mainbodyimg{
  width: 960px;
  height: 960px;
  cursor: inherit;
}
.maincontent {
  color: #fff;
  display: inline-block;
  text-align: center;
  position: relative;
  top: -850px;
  left: 700px;
  text-shadow: 0 2px 7px rgba(0, 0, 0, 0.6);
}
.maincontent h1 {
  font-size: 4.5em;
}
.choiceMenu {
  display: flex;
  flex-direction:row;
}
.choiceMenu li {
  width: 300px;
  margin: auto;
  text-align: center;
  height: 50px;
}
.choiceMenu li a {
  padding: 15px;
  background-color: #e0cdb6;
  border-radius: 20px;
  border: 1px solid;
}
.choiceMenuImg {
  display: flex;
  flex-direction:row;
}
.choiceMenuImg li img{
  width: 480px;
  height: 480px;
}

.maincontents {
  background-color:  rgb(255, 248, 232);
  margin: 5px auto;
  padding: 50px;
  border-radius: 10px;
}


.map {
  width: 700px;
  margin: 20px auto;
}
.map h4 {
  padding-left: 50px;
  width: 700px;
  border-bottom: 1px solid;
  margin-top: 50px;
  font-size: 1.2em;
  line-height: 3.3;
}
.map div {
  display: inline-block;
}
.map > div img {
  width: 650px;
  margin: 30px 30px 0;
}
.map p {
  display: inline-block;
  height: 30px;
}
#smallicon {
  width: 100px;
  margin-right: 10px;
}
.maincontents h3 {
  display: block;
  font-size: 1.8em;
  border-bottom: 1px solid;
  padding-left: 75px;
  line-height: 3.3;
  margin: 10px 30px;
}
.text {
  position: relative;
  line-height: 2.5em;
  margin: 10px auto;
  width: 700px;
  background-color: #e0cdb6;
  border-radius: 30px;
  padding: 10px;
  box-shadow: 3px 5px 10px gray;
}
.text h4 {
  border-bottom: 1px solid;
  padding-left: 20px;
  font-size: 1.2em;
}
.text h6 {
  font-size: 1.0em;
  padding-left: 5px;
}
.text p {
  padding-left: 10px;
  line-height: 1.5em;
  font-size: 0.8em;
}

.mapview {
  width: 700px;
  padding: 0 70px;
}
.mapview iframe {
  width: 700px;
  height: 500px;
  border: 1px solid #ccc;
}
.mapview img {
  width: 700px;
}
.newMenu {
  padding-bottom: 10px;
}
.newMenu p {
  padding: 0 250px;
}
.newMenu p img {
  width: 450px;
  cursor: inherit;
}
.instartext {
  width: 700px;
  padding: 50px;
  margin: auto;
  border: 1px solid #fff;
  background-color: #e5d7c5;
  cursor: inherit;
}
.instartext h1 {
  background-color: #fff;
  width: 200px;
  text-align: center;
  border-radius: 50px;
  padding: 10px;
  margin-bottom: 10px;
  float: left;
  top: -30px;
  position: relative;
}
.instartext h1 img {
  width: 30px;
  cursor: inherit;
}
.instartext p {
  position: relative;
  float: left;
  top: -10px;
  width: 390px;
  padding-left: 30px;
  font-weight: bold;
}