.main2 {
  display: block;
  padding: 0;
  margin: 0;
}
.main2 > img {
  width: 960px;
  height: 350px;
  cursor: inherit;
}

#main2_content1 {
  background-color:  rgb(255, 248, 232);
  position: relative;
  margin-bottom: 5px;
  padding: 10px;
  border-radius: 10px;
}

.box1 {
  width: 900px;
  margin: 10px auto;
}
.box1 h3 {
  display: block;
  font-size: 1.8em;
  border-bottom: 1px solid;
  padding-left: 75px;
  line-height: 3.3;
  margin: 10px 30px;
}
.box2 {
  display: flex;
  margin: 35px auto;
  line-height: 2.0;
  flex-direction: column;
  width: 700px;
}
.box2head {
  font-size: 1.3em;
  border-bottom: 1px solid;
  font-weight: bold;
  padding-left: 25px;
}
.boxstyle {
  display: flex;
  text-align: center;
  font-weight: bold;
  font-size: 0.8em;
}
.boxstyle img {
  width: 150px;
  height: 150px;
  margin: 10px;
  border-radius: 15px;
  cursor: inherit;
}
.boxstyle li p {
  text-shadow: 0 2px 3px rgba(18, 153, 231, 0.4);
}
.boxstyle1 {
  display: block;
}
.boxstyle1 div {
  display: flex;
  flex: row;
}
#view {
  width: 500px;
  margin: 20px auto;
}
#view img {
  width: 500px;
  cursor: inherit;
}
#view h1 {
  text-shadow: 0 2px 3px rgba(18, 153, 231, 0.4);
  text-align: center;
}
.viewText {
  position: relative;
  float: left;
  text-align: center;
  padding-left: 50px;
}
.viewText1 {
  width: 350px;
  font-weight: bold;
}
.viewText2 {
  width: 100px;
}